* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
   background: #fff; 
   background-repeat: no-repeat;
  height: 100vh;
  max-width: 1200px;
  margin: 0 auto;
}

/*Main*/

.main-container {
  width: 100%;
  height: 100%;
}

/* Nav */

.nav-wrapper {
  width: 100%;
  height: 9vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}
.nav-wrapper.active {
  background: rgb(255, 255, 255);
}

.nav-container {
  z-index: 1;
  display: flex;
  flex-direction: row;
  padding: 2rem;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 9vh;
  margin: 0 auto;
  max-width: 1200px;
}


.link {
  list-style-type: none;
  float: left;
}

.nav-links .anchor {
  display: inline-block;
  color: black;
  text-decoration: none;
  cursor: pointer;
  font-family: "Varela Round", sans-serif;
  font-size: 1.1rem;
  letter-spacing: 0.7px;
  font-weight: 550;
  line-height: 2rem;
  border-bottom: 3px solid transparent;
  border-radius: 2px;
  transition: all 0.3s ease;
  margin: 0.8rem;
}

.nav-links .anchor:hover {
  border-color: #55198b;
}

.nav-links .scrolled {
  color: black;
}

.nav-links.active .anchor {
  color: black;
}

.anchor.active {
  font-weight: 570;
  border-color: #55198b;
}

.menu-icon {
  display: none;
}

/* Home */
.home-container {
  font-family: "Varela Round", sans-serif;
  display: flex;
  color: black;
  flex-direction: row;
  height: 100%;
  padding: 1rem;
  align-items: center;
  width: 100%;
  min-height: 100vh;
}

.content-container {
  width: 50vw;
  height: 100%;
  flex-direction: column;
  display: flex;
  padding: 1.8rem;
  margin-top: 3rem;
  justify-content: center;
}

.main-content {
  font-size: 4rem;
}

.skill-content {
  margin: 0.6rem 0;
  color: #9c72be;
  font-family: "Varela Round", sans-serif;

  font-weight: 400;
  font-size: 1.5rem;
}

.skill-animation {
  height: 60px;
  width: 100%;
  margin: 1rem 0;
}

.hand {
  display: inline-block;
  margin: 0.9rem;
  cursor: pointer;
}

.welcome-content {
  color: gray;
}

.image-container {
  margin-top: 1.5rem;
  width: 50vw;
  height: 36rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.image {
  height:50%;
}

/* About Me */

.aboutme-container {
  align-items: center;
  justify-content: center;
  display: flex;
  width: 100%;
  height: 100vh;
  flex-direction: column;
  font-family: "Varela Round", sans-serif;
  font: weight lighter; ;
  color: rgb(0, 0, 0);
  font-size: 1.3rem;
  padding: 2rem;
}

.aboutme-wrapper {
  color:black ;
  align-items: center;
  justify-content: center;
  display: flex;
  width: 60%;
  height: 40vh;
  flex-wrap: wrap;
  flex-direction: row;
  
}

/* Portfolio */
.portfolio-container {
  font-family: "Exo 2", sans-serif;
  display: flex;
  color: rgb(0, 0, 0);
  font-size: 1.3rem;
  flex-direction: column;
  padding: 2rem;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  min-height: 100vh;
}

.portfolio-container h1 {
  margin: 3rem 0;
}

.projects-container {
  min-height: 60vh;
  width: 100%;
  flex-wrap: wrap;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.projects {
  height: 23rem;
  width: 21rem;
  margin: 1rem;
  /* background: #e7dddd5c; */
  box-shadow: 3px 2px 10px gray;
  cursor: pointer;
  border-radius: 10px;
  transition: all 0.2s ease;
}

.project-images {
  border-radius: 10px;
  cursor: pointer;
  width: 100%;
  border-bottom: 1px solid #55198b;
  height: 70%;
}

/* Images Proportion (877 * 679)*/

#projects{
  background-image: url("./images/proyecto.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

.project-links {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 30%;
  justify-content: center;
  align-items: center;
}

.project-links .text {
  width: 100%;
  height: 50%;
  color: black;
  font-family: "Varela Round", sans-serif;
  font-size: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.project-links .icons {
  width: 100%;
  height: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}




.icons i {
  color: white;
  cursor: pointer;
  display: inline-block;
  font-size: 1.3rem;
  height: 2.6rem;
  line-height: 2.6rem;
  position: relative;
  text-align: center;
  user-select: none;
  width: 2.6rem;
  border-radius: 2.6rem;
  margin: 0px 5px 10px;
  transition: all 0.2s ease-in 0s;
}

/* Contact */
.contact-container {
  font-family: "Exo 2", sans-serif;
  display: flex;
  color: rgb(0, 0, 0);
  font-size: 1.3rem;
  flex-direction: column;
  padding: 2rem;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  min-height: 100vh;
}

.profile-image {
  margin: 1rem;
  box-shadow: 6px 6px 10px gray;
  width: 280;
  border-color: black;
  border-radius: 50%;
  background-size: cover;
  height: 280;
}

.cards-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
  align-items: center;
  min-height: 40vh;
}

.cards {
  align-items: center;
  justify-content: center;
  text-align: center;
  display: flex;
  box-shadow: 3px 2px 10px;
  border-radius: 20px;
  flex-direction: column;
  width: 16rem;
  height: 9rem;
  margin: 1rem;
  padding: 1.3rem 0;
}

.contact-icons {
  display: flex;
  align-items: center;
  width: 100%;
  height: 33.33%;
  text-align: center;
  justify-content: center;
}

.contact-text {
  display: flex;
  margin-top: 0.7rem;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue";
  height: 33.33%;
  font-size: 1rem;
  align-items: center;
  justify-content: center;
}

.contact-links {
  margin-top: 0.7rem;
  display: flex;
  font-size: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue";
  width: 90%;
  height: 33.33%;
  align-items: center;
  justify-content: center;
}

.contact-links > span {
  display: block;
}

.contact-links i {
  cursor: pointer;
  margin: 0.3rem;
  display: block;
  font-size: 1.3rem;
  position: relative;
  text-align: center;
  transition: all 0.3s ease;
  padding: 0.1rem;

  user-select: none;
}

.contact-links a {
  text-decoration: none;
  color:black;
}

.contact-links i:hover {
  transform: scale(1.09);
  color: #55198b;
}

.contact-links #github {
  color: rgb(0, 0, 0);
}

.contact-links:hover #github {
  color: #55198b;
}

.contact-links #facebook {
  color: rgb(0, 0, 0);
}

.contact-links:hover #facebook {
  color: #55198b;
}

.contact-links #instagram {
  color: rgb(0, 0, 0);
}

.contact-links:hover #instagram {
  color: #55198b;
}

.contact-links #linkedin {
  color: rgb(0, 0, 0);
}

.contact-links:hover #linkedin {
  color: #55198b;
}

.contact-links #twitter {
  color: rgb(0, 0, 0);
}

.contact-links:hover #twitter {
  color: #55198b;
}

.contact-icons i {
  color: #55198b;
  cursor: pointer;
  font-size: 1.7rem;
  position: relative;
  user-select: none;
  transition: all 0.2s ease-in 0s;
}

/* General */

.button-wrapper {
  width: 100%;
}

.button:hover {
  background-color: #55198b;
  color: #fff;
  transition: all 0.3s ease 0s;
  transform: translateY(-3px);
}

.button-link {
  text-decoration: none;
}

.button {
  background-color: whitesmoke;
  border: 1px solid whitesmoke;
  color: #55198b;
  font-weight: 700;
  min-width: 200px;
  padding: 13px 22px;
  text-transform: uppercase;
  border-radius: 6px;
  text-align: center;
  text-decoration: none;
  outline: none;
  margin-top: 3.6rem;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease-in-out 0s;
}

.contact-btn {
  margin-right: 1rem;
}

.heading {
  margin: 2rem 0;
  font-family: "Varela Round", sans-serif;
  color: 55198b;
}

/* Media Queries */
/* 1150 Breakpoint */
@media screen and (max-width: 1150px) {
  .home-container {
    flex-direction: column;
  }
  .content-container {
    padding-top: 5rem;
    width: 100vw;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .svg-container {
    width: 100vw;
  }
}

/*900 Mobile Nav Version */

@media screen and (max-width: 900px) {
  .nav-links {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 350px;
    position: absolute;
    top: 9vh;
    text-align: center;
    align-items: center;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-links.active {
    background: rgba(255, 255, 255, 0.911);
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .menu-icon {
    display: block;
    position: relative;
    margin: 0 1rem;
    font-size: 1.8rem;
    cursor: pointer;
  }
}

/* 700 */

@media screen and (max-width: 700px) {
  .svg-container {
    margin-top: 1.5rem;
    width: 50vw;
    height: 36rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .svg {
    height: 60%;
  }
}

/* 500 */
@media screen and (max-width: 500px) {
  button {
    width: 90%;
  }

  .contact-btn {
    margin-right: 0;
  }
}

/* 550 */
@media screen and (max-width: 550px) {
  .content-container {
    font-size: 0.7rem;
  }

  .main-content {
    font-size: 2rem;
  }

  .skill-content {
    margin: 0.6rem 0;
    font-size: 1.2rem;
  }

  .hand {
    font-size: 1.8rem;
  }

  .welcome-content {
    font-size: 0.7rem;
  }
}

/* 500 */
@media screen and (max-width: 500px) {
  .svg {
    height: 40%;
  }
}

/* 415 */

@media screen and (max-width: 415px) {
  .skill-content {
    font-size: 1rem;
  }
}

/* 395*/

@media screen and (max-width: 395px) {
  .heading {
    font-size: 1.2rem;
  }
  .button {
    font-size: 0.8rem;
  }
}
